@import "colours";

.front-header {
    height: 100%;
    background-color: $base-light;
    background-repeat: no-repeat;
    min-height: 100vh;
    .main {
        margin: 0 auto;
        // margin-top: 4em
        padding: 1.8em;
        max-width: 1200px;
        background-color: #f5f5f5;
        border-radius: 1em;
        h1 {
            margin-top: 0; }
        p {
            font-size: 1.1em;
            line-height: 1.5em; }
        @media (max-width: 576px) {
            margin: 0.5em;
            padding: 1em; }
        @media (min-width: 576px) {
            margin: 1em 1em;
            p {
                font-size: 1.2em; } }
        @media (min-width: 768px) {
            margin: 2em 2em;
            p {
                font-size: 1.3em; } }
        @media (min-width: 992px) {
            margin: 2.5em 2.5em; }
        @media (min-width: 1200px) {
            margin: 4em auto;
            p {
                font-size: 1.4em; } }
        .me {
            color: $me-light-dark;
            font-style: italic; }
        .zon {
            color: $zon-light; }
        .akyla {
            color: $akyl-light-dark; }
        .creative-orange {
            color: $crea-orange-light-dark; }
        .landstede {
            color: $lands-light-dark; }
        .e-active {
            color: $active-light-dark; }
        .windes {
            color: $windes-light-dark; }
        .azerty {
            color: $azzy-light-dark; }
        .dielle {
            color: $diel-light-dark; }
        .euron {
            color: $euro-light-dark; }
        .egm {
            color: $egm-light-dark; }
        .twitter {
            color: $tweet-light-dark; }
        .linkedin {
            color: $link-light-dark; }
        .wehkamp {
            color: $link-wehkamp; }
        .iq, .github, .gpg, .ssh, .lnu, .uu {
            color: $base-dark; } } }
@media (prefers-color-scheme: dark) {
    .front-header {
        background-color: $base-dark;
        .main {
            background-color: $base-dark;
            color: $base-light;
            .iq, .github, .key {
                color: $base-light; }
            .zon {
                color: $zon-dark; }
            .lnu {
                color: $link-lnu; }
            .uu {
                color: $link-uu; } } } }
